.devices {
  display: flex;
  justify-content: center;
  overflow: hidden;

  @media (min-width: $media-large) { 
    overflow: visible;
  }


  &__inner {
    padding: 0 28px;
    max-width: 640px;
    width: 100%;
  }


  &__mockup {
    margin-top: -15px;
    width: 100%;
    padding-top: 100%;
    position: relative;
  }

  video {
    position: absolute;
    left: 50%;
    height: 100%;
    top: 0;
    transform: translate(-50%, 0%);
  }

  .device {
    position: absolute;
    overflow: hidden;
  }


  .imac {
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;  

    .svg {
      width: 100%;
      position: relative;
      z-index: 1;
    }

    video {
      position: absolute;
      left: 50%;
      width: 75%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }


  .ipad {
    z-index: 1;
    bottom: 0.5%;
    width: 61.66%;
    left: 12%;

    .svg {
      width: 100%;
      position: relative;
      z-index: 1;
    }

    .video-container {
      overflow: hidden;
      position: absolute;
      width: 72%;
      height: 64.3%;
      top: 18%;
      left: 14%;
    }

    video {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }
  .iphone {
    z-index: 2;
    bottom: 0;
    right: 12.5%;
    width: 44.125%;

    .svg {
      width: 100%;
      position: relative;
      z-index: 1;
    }

    .video-container {
      overflow: hidden;
      position: absolute;
      width: 50%;
      height: 75%;
      border-radius: 10%;
      top: 12%;
      left: 25%;
    }
  }
}


[data-aos="imac-appearance"],
[data-aos="ipad-appearance"],
[data-aos="iphone-appearance"] {
  opacity: 0;
  transition-timing-function: ease-in-out;
  transition-property: transform, opacity;

  &.aos-animate {
    opacity: 1;
    transform: translateZ(0)
  }
}

[data-aos="imac-appearance"] {
  transform: translate3d(0,-100%,0);
}

[data-aos="ipad-appearance"] {
  transform: translate3d(-100%,0,0);
}

[data-aos="iphone-appearance"] {
  transform: translate3d(100%,0,0);
}