.first-screen {
  position: relative;
  min-width: $container-min-width;
  &__video-wrapper {
    max-width: 100%;
    height: 100vh;
    position: sticky;
    top: 0;
    overflow: hidden;
    background: #000;
  }


  &__video {
    position: absolute;
    min-height: 100%;
    min-width: 100%;
    left: 50%;
    top: 0;
    transform: translate(-50%, 0%);
  }

  &__inner {
    position: relative;
    margin-top: -100vh;
    z-index: 2;
  }

  &__content {
    padding: 0 20px;

    @media (min-width: $media-medium) {
      padding: 0 24px;
    }

    @media (min-width: $media-large) {
      padding: 0;
    }
  }

  .one, .two {  
    height: 100vh;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;

    img {
      width: 40px;
      height: 40px;
      margin-bottom: 20px;
    }

    h2 {
      max-width: 464px;
      font-weight: 800;
      font-size: 28px;
      line-height: 33px;
    }
  }
}


[data-aos="new-animation"] {

  @media screen and (min-width: 768px) {
    transform: translate(-50%, 0%) scale(1);

    &.aos-animate {
      transform: translate(-50%, 0%) scale(0.5);;
    }
  }
}