.cta {
  position: relative;
  min-width: $container-min-width;

  &__inner {
    position: relative;
  }

  &__content {
    color: #fff;
    padding: 40px 12px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media (min-width: $media-medium) {
      padding: 40px 24px;
    }
    @media (min-width: $media-large) {
      padding: 40px 80px;
    }
  }

  &__background {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(162.95deg, rgba(216, 114, 53, 0.7) 0%, rgba(244, 199, 96, 0.7) 100%), #FCF8E3;

    @media (min-width: $media-large) {
      margin: 0 auto;
      max-width: 860px;
      border-radius: 16px 16px 0 0;
    }
  }

  &__image {
    width: 64px;
    height: 64px;
    margin-bottom: 16px;
  }

  &__heading {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    margin-bottom: 8px;
  }

  &__description {
    font-family: "Inter", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    margin-bottom: 32px;
  }

  &__buttons {
    height: 60px;
    width: 100%;
    background: black;
  }
}